@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

/*
 * Viewport sized typography with minimum and maximum values
 *
 * @author Eduardo Boucas (@eduardoboucas)
 *
 * @param {Number}   $responsive  - Viewport-based size
 * @param {Number}   $min         - Minimum font size (px)
 * @param {Number}   $max         - Maximum font size (px)
 *                                  (optional)
 * @param {Number}   $fallback    - Fallback for viewport-
 *                                  based units (optional)
 *
 * @example scss - 5vw font size (with 50px fallback), 
 *                 minumum of 35px and maximum of 150px
 * @include responsive-font(5vw, 35px, 150px, 50px);
 */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 1em;
  background-color: white;
  width: 7em;
  height: 9em; }

.status {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0.5em;
  left: 0.5em;
  width: 1em;
  height: 1em; }

.spinner {
  width: 1em;
  height: 1em;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  border: solid 2px;
  border-color: #4055ae #4055ae transparent transparent; }

.checkmark {
  display: inline-block; }
  .checkmark:after {
    content: '';
    display: block;
    width: 0.6em;
    height: 0.9em;
    margin-top: -0.25em;
    border: solid #4055ae;
    border-width: 0 0.2em 0.2em 0;
    transform: rotate(45deg); }

.crossmark {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }
  .crossmark:before, .crossmark:after {
    content: '';
    width: 0.2em;
    height: 1em;
    background: #ea29a9; }
  .crossmark:before {
    margin-right: -1.5px;
    transform: rotate(45deg); }
  .crossmark:after {
    margin-left: -1.5px;
    transform: rotate(135deg); }

.image {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 6em;
  height: 6em;
  flex-shrink: 0; }
  .image > img {
    width: 100%;
    height: 100%; }
    .image > img[data-erc20] {
      width: 70%;
      height: 70%; }

.content {
  margin: 0;
  font-weight: bolder;
  color: #4055ae; }
