@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

/*
 * Viewport sized typography with minimum and maximum values
 *
 * @author Eduardo Boucas (@eduardoboucas)
 *
 * @param {Number}   $responsive  - Viewport-based size
 * @param {Number}   $min         - Minimum font size (px)
 * @param {Number}   $max         - Maximum font size (px)
 *                                  (optional)
 * @param {Number}   $fallback    - Fallback for viewport-
 *                                  based units (optional)
 *
 * @example scss - 5vw font size (with 50px fallback), 
 *                 minumum of 35px and maximum of 150px
 * @include responsive-font(5vw, 35px, 150px, 50px);
 */
.container {
  padding: 2em 20vw; }

.title {
  color: #eee; }

.mission {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  padding: 1em; }

.requirements {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-start;
  overflow: scroll; }
  .requirements > div {
    margin-left: 1em;
    flex-shrink: 0; }

.divider {
  margin: 0 2em 0 auto;
  padding-left: 2em;
  border-right: dashed 1px #eee; }

.reward {
  margin-right: 2em;
  flex-shrink: 0; }

.get {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 5em;
  height: 3em;
  padding: .5em;
  margin: auto 0;
  font-weight: bold;
  color: #eee;
  background-color: #cc148e;
  border-radius: 0.5em;
  border: none;
  outline: none;
  transition: .24s ease-out;
  cursor: not-allowed; }
  .get > h4, .get p {
    margin: 0; }
  .get > p {
    font-size: 0.75em;
    color: #bbbbbb; }
  .get[data-active="true"] {
    cursor: pointer; }
    .get[data-active="true"]:hover {
      transform: scale(1.1); }

.viewTx {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 5em;
  flex-shrink: 0;
  height: 3em;
  padding: .5em;
  margin: auto 0;
  font-weight: bold;
  background-color: #5d71c4;
  border-radius: 0.5em;
  transition: .24s ease-out;
  cursor: pointer; }
  .viewTx:hover {
    transform: scale(1.1); }
  .viewTx > h5 {
    color: #eee; }
